import rightNav from '../../components/rightNav/index'
import qrcode from '../../components/rightNav/qrcode'
import bannerPop from '../../components/pop/banner'
export default {
  components: {
    rightNav,
    qrcode,
    bannerPop
  },
  async asyncData (vm) {
    /*
    * vm.params带有一个参数：
    * id 新闻id
    * */
    let detailRes, detail
    detailRes = await vm.$requests.entityNewsDetail({
      news_id: vm.params.id
    })
    if (detailRes.status === 200 && detailRes.data.result === 'success') {
      detail = detailRes.data.data
    }
    return {
      detail,
      bannerPopShow: false // 弹窗是否展示
    }
  },
  methods: {
    handleBannerPopShow () {
      this.bannerPopShow = true
    },
    handleBannerPopHide () {
      this.bannerPopShow = false
    }
  }
}
